import React from 'react';
import styled from "styled-components";
import {Area, AreaChart, Line, LineChart, Tooltip, XAxis, YAxis,} from 'recharts';
import ScaleChanger from '../ScaleButtons/ScaleButtons'
import {connect} from "react-redux";
import CartesianGrid from "recharts/lib/cartesian/CartesianGrid";


const Card = styled.div`
	background: rgb(40, 44, 52) none repeat;
	width: max-content;
	height: max-content;
	padding: 22px;
	border-radius: 15px;
	box-shadow: 0 0 17px rgba(0,0,0,0.7);
		margin: auto;
`;
const TopBar = styled.div`
	display: flex;
	@media screen and (max-width: 1024px){
		flex-direction: column;
		width: max-content;
	}
padding-bottom: 10px;
	height: max-content;
	justify-content: space-between;
`;
const Title = styled.h3`
	color: #eeeeee;
	font-size: 3em;
	padding: 0 0 .6em;
	margin: 0 0 0 .5em;
	font-family: Montserrat,sans-serif;
	font-weight: 900;
`;
const size = {
	width: window.innerWidth > 1024 ? ((window.innerWidth / 1.129411764705882) - 44 * 3)  : window.innerWidth * 0.84,
	height: 400,
};

function BigChart(props) {
	const {title, fill} = props;
	return (
		<Card>
			<TopBar>
				<Title>{title}</Title>
				<ScaleChanger/>
			</TopBar>
			{fill && (<AreaChart
				{...size}
				data={props.data}
			>
				<CartesianGrid stroke="#eee" strokeDasharray="2 2"/>
				<XAxis dataKey="date"/>
				<YAxis/>
				<Tooltip itemStyle={{"color": "#333;"}}/>
				<Area type="monotone" dataKey="count" stroke="#f7f7f7" fill="#fff"/>
			
			</AreaChart>)}
			{!fill && (<LineChart {...size}
			                      data={props.data}
			>
				<XAxis dataKey="date"/>
				<YAxis/>
				<Tooltip itemStyle={{"color": "#333;"}}/>
				<Line type="monotone" dataKey="count" stroke="#f7f7f7" strokeWidth={2}/>
			</LineChart>)}
		</Card>
	);
}


const mapStateToProps = state => ({
	scale: state.scale,
	data: state.data.data,
});
export default connect(mapStateToProps)(BigChart);