import React, {Component, Fragment} from 'react';
import styled, {keyframes} from 'styled-components';
import {fadeIn} from 'react-animations';

import './App.css';

import Chart from './components/Chart/chart-component'
import BigChart from './components/BigChart/BigChart-component'
import {fetchDataStart} from "./redux/data/data.actions";
import {connect} from "react-redux";
import SimpleCardComponent from "./components/SimpleTextCard/SimpleTextCard";


const bounceAnimation = keyframes`${fadeIn}`;

const BouncyDiv = styled.div`
  animation: 1s ${bounceAnimation};
  box-sizing: border-box;
  @media screen and (max-width: 1024px){
		width: 100vw;
		margin: auto;
		height: max-content;
	}
`;

//props.dispatchData()
class App extends Component {
	
	UNSAFE_componentWillMount() {
		this.props.dispatchData()
	}
	
	
	render() {
		return (
			<Fragment>
				<ChartsBlock>
					<BouncyDiv>
						<BigChart title={"Using chart"} />
					</BouncyDiv>
					<ChartGrid>
						<BouncyDiv>
							<Chart title={"Errors chart"} dataKey={"count"}/>
						</BouncyDiv>
						<BouncyDiv>
							<Chart title={"New Users"} fill dataKey={"userCount"}/>
						</BouncyDiv>
						<BouncyDiv>
							<SimpleCardComponent title={"Total count:"} data={this.props.count} subText={"users"}/>
						</BouncyDiv>
					</ChartGrid>
				</ChartsBlock>
			</Fragment>
		);
	}
}


const ChartsBlock = styled.div`
  box-sizing: border-box;
  height: max-content;
  width: max-content;
  margin: 3vh auto;
`;
const ChartGrid = styled.div`
	margin-top: 1em;
	display: grid;
  	grid-template-columns: 1fr 1fr 1fr;
  	@media screen and (max-width: 1024px){
  		grid-template-columns: 1fr;
  	}
  	
  	grid-gap:1em ;
  	max-width: 90vw;
`;

const mapDispatchToProps = dispatch=>({
	dispatchData:()=> dispatch(fetchDataStart())
});
const mapStateToProps = state=>({
	count:state.data.count
})
export default connect(mapStateToProps, mapDispatchToProps)(App);
