import React from 'react';
import styled from "styled-components";

const Card = styled.div`
	background: rgb(40, 44, 52) none repeat;
	width: 28vw;
	height: 24vw;
	box-sizing: border-box;
	@media screen and (max-width: 1024px){
		width: 90vw;
		margin: auto;
		height: 90vw;
	}
	padding: 22px;
	border-radius: 15px;
	box-shadow: 0 0 17px rgba(0,0,0,0.7);
`;
const Title = styled.h3`
	color: #fff;
	font-size: 3em;
	padding: 0 0 .6em;
	margin-right: auto;
	font-family: Montserrat,sans-serif;
	font-weight: 900;
`;
const Text = styled.span`
	color: #eaeaea;
	font-size: 6em;
	padding: 0 0 .6em;
	margin: 0  0 ;
	@media screen and (max-width: 1024px){
		font-size: 4em;
	}
	font-family: Montserrat,sans-serif;
	font-weight: 900;
`;
const SubText = styled.p`
	color: #a7a7a7;
	font-size: 3em;
	padding: 0 0 .6em;
	margin: 0 0 0 .5em;
	@media screen and (max-width: 1024px){
		font-size: 2em;
	}
	font-family: Montserrat,sans-serif;
	font-weight: 200;
`;
const Textblock = styled.div`
	margin: 0 auto 0 .5em;
	display: flex;
	align-items: baseline;
	width: max-content;
`

function SimpleCardComponent(props) {
	const {title, data} = props;
	console.log(props)
	return (
		<Card>
			<Title>{title}</Title>
			<Textblock>
				<Text>
					{data}
				</Text>
				<SubText>{props.subText ? props.subText : ""}</SubText>
			</Textblock>
		</Card>
	);
}


export default SimpleCardComponent;