import React from 'react';
import styled from "styled-components";
import {Area, AreaChart, CartesianGrid, Legend, Line, LineChart, Tooltip, XAxis, YAxis,} from 'recharts';
import {connect} from "react-redux";

const Card = styled.div`
	background: rgb(40, 44, 52) none repeat;
	width: max-content;
	height: max-content;
	padding: 22px;
	margin: 0 auto;

	border-radius: 15px;
	box-shadow: 0 0 17px rgba(0,0,0,0.7);
`;
const Title = styled.h3`
	color: #fff;
	font-size: 2em;
	padding: 0 0 .6em;
	margin: 0 0 0 .5em;
	font-family: Montserrat,sans-serif;
	font-weight: 900;
`;
const size = {
	width: window.innerWidth>1024?((window.innerWidth / 1.129411764705882) - 44 * 3) / 3:window.innerWidth*0.85,
	height: 400,
};
function Chart(props) {
	const {data,title, fill,  dataKey} = props;
	//const data = sliceData(props.scale.id, props.data);
	return (
		<Card>
			<Title>{title}</Title>
			{fill && (<AreaChart
				{...size}
				data={data}
			>
				<CartesianGrid strokeDasharray="3 3"/>
				<XAxis dataKey="date"/>
				<YAxis/>
				<Tooltip itemStyle={{"color":"#333;"}}/>
				<Legend/>
				<Area type="monotone" dataKey={dataKey} stroke="#f7f7f7" fill="#fff"/>
			</AreaChart>)}
			{!fill && (<LineChart {...size}
			                      data={data}
			                     >
				<Legend verticalAlign="top" height={36}/>
				<XAxis dataKey="date"/>
				<YAxis/>
				<Tooltip itemStyle={{"color":"#333;"}}/>
				<Line type="monotone" dataKey={dataKey} stroke="#f7f7f7" strokeWidth={2}/>
			</LineChart>)}
		</Card>
	);
}

const mapStateToProps = state => ({
	data: state.data.data,
	scale:state.scale
});
export default connect(mapStateToProps)(Chart);