import dataActionTypes from './data.types';

const INITIAL_STATE = {
	data: null,
	count:0,
	isFetching: false,
	error: undefined
};

const dataReducer = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case dataActionTypes.FETCH_DATA_START:
			return {
				...state,
				isFetching: true
			};
		case dataActionTypes.FETCH_DATA_SUCCESS:
			return {
				...state,
				isFetching: false,
				data: action.payload
			};
		case dataActionTypes.FETCH_COUNT_SUCCESS:
			return {
				...state,
				isFetching: true,
				count: action.payload
			};
		case dataActionTypes.FETCH_DATA_FAILURE:
			return {
				...state,
				isFetching: false,
				error: action.payload
			};
		default:
			return state;
	}
};

export default dataReducer;
