import { load } from "redux-localstorage-simple"

const INITIAL_STATE = load().scale?load().scale : {
	id: 3,
	scaleType:"all"
};

const scaleReducer = (state = INITIAL_STATE, action) => {
	switch (action.type) {
		case "SET_SCALE":
			
			return {
				...action.payload
			};
		default:
			return state;
	}
};

export default scaleReducer;
