import { takeLatest, call, put, all } from 'redux-saga/effects';

import {
	fetchDataSuccess,
	fetchDataFailure,
	fetchCountSuccess
} from './data.actions';

import DataActionTypes from "./data.types";

export function* fetchDataAsync() {
	try {
		const json = yield fetch('https://statbotservis.glitch.me/last').then(response => response.json());
		yield put(fetchDataSuccess(json));
	} catch (error) {
		yield put(fetchDataFailure(error.message));
	}
}

export function* fetchDataStart() {
	yield takeLatest(
		DataActionTypes.FETCH_DATA_START,
		fetchDataAsync
	);
}
export function* fetchCountAsync() {
	try {
		const json = yield fetch('https://statbotservis.glitch.me/count').then(response => response.json());
		yield put(fetchCountSuccess(json));
	} catch (error) {
		yield put(fetchDataFailure(error.message));
	}
}

export function* fetchCountStart() {
	yield takeLatest(
		DataActionTypes.FETCH_DATA_START,
		fetchCountAsync
	);
}

export function* dataSagas() {
	yield all([call(fetchDataStart),call(fetchCountStart)]);
}
