import React, {Component} from "react";
import styled from "styled-components";
import {connect} from "react-redux";

 class ScaleButtons extends Component {
	constructor(props){
		super(props);
		this.state={
			activeId: props.scale.id
		}
	}
	componentDidMount() {
		document.getElementById("1").classList.remove('active');
		document.getElementById("2").classList.remove('active');
		document.getElementById("3").classList.remove('active');
		document.getElementById(this.state.activeId).classList.add('active');
	}
	componentDidUpdate(prevProps, prevState, snapshot) {
		document.getElementById("1").classList.remove('active');
		document.getElementById("2").classList.remove('active');
		document.getElementById("3").classList.remove('active');
		document.getElementById(this.state.activeId).classList.add('active');
	}
	
	setActiveHandler = (e) => {
		e.preventDefault();
		const typeMap = ["week","month","all"];
		this.setState({activeId:e.target.id});
		this.props.setScale(e.target.id,typeMap[e.target.id-1])
	};
	render() {
		
		return (
			<ScaleButtonsStyled>
				<Sbtn id="1" onClick={this.setActiveHandler}>Week</Sbtn>
				<Sbtn id="2" onClick={this.setActiveHandler}>Month</Sbtn>
				<Sbtn id="3" onClick={this.setActiveHandler}>All</Sbtn>
			</ScaleButtonsStyled>
		)
	}
};
 const mapStateToProps = state=> ({
	 scale: state.scale
 });
 const mapDispatchToProps = dispatch=>({
	setScale:(id,scaleType)=>{
		dispatch({
			type: "SET_SCALE",
			payload: {id,scaleType}
		})
	}
 })
export default connect(mapStateToProps,mapDispatchToProps)(ScaleButtons)


const ScaleButtonsStyled = styled.div`
	border-radius: 12px;
	border: #eee 1px solid;
	color: #eee;
	display: flex;
	height: min-content;
	width:max-content;
	padding: 0;
`;
const Sbtn = styled.span`
	background: none;
	padding: .2em 1em;
	transition: .3s all ease;
	
	:first-of-type{
		border-right: 1px solid #eee;
		border-radius: 10px 0 0 10px;
	}
	:last-of-type{
		border-left: 1px solid #eee;
		border-radius: 0 10px 10px 0;
	}
	:hover, &.active{
		background: #eee;
		color: #13191d;
	}
`;
