import dataActionTypes from './data.types';


export const fetchDataStart = () => ({
	type: dataActionTypes.FETCH_DATA_START
});

export const fetchDataSuccess = dataMap => ({
	type: dataActionTypes.FETCH_DATA_SUCCESS,
	payload: dataMap
});

export const fetchDataFailure = errorMessage => ({
	type: dataActionTypes.FETCH_DATA_FAILURE,
	payload: errorMessage
});

export const fetchCountSuccess = countMap =>({
	type: dataActionTypes.FETCH_COUNT_SUCCESS,
	payload: countMap
})
